}

.battleship-tile{
  background-color: rgb(14, 211, 14);
}

.destroyer-tile{
  background-color: rgb(248, 244, 4);
}

.submarine-tile{
  background-color: rgb(13, 231, 231);
}

.patrolBoat-tile{
  background-color: rgb(245, 160, 3);
}

.glow{
  background-color:rgb(226, 233, 238);
}

.glow:hover{
  background-color: rgb(226, 233, 238);;
}

</style>




